.App {
  MAX-WIDTH: 1000px;
  margin: 0 auto;
  border: 2px solid #ddd;
  display: flex;
  flex-direction: column;
  height: 99vh;
}

.App-header {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

h1, img {
  text-align: center;
  margin: 0 auto;
}

.categories-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  justify-content: center;
  gap: 5px;
  margin: 2px;
}

button {
  cursor: pointer;
  border: 2px solid #ddd;
  padding: 5px;
  font-size: 15px;
  border-radius: 0;
}

button:hover {
  background: #ddd;
}

.main-random-button {
  border: none;
  background: linear-gradient(to left, black, #39393a);
  padding: 20px;
  font-size: 25px;
  color: #ddd;
  margin: 2px;
}

.main-random-button:hover{
  background: linear-gradient(to left, #292929, #6c6c6d);
}

.fact-container {
  border: 5px solid;
  display: flex;
  height: 50vh;
  justify-content: center;
  font-size: 25px;
  padding: 20px;
  text-align: center;
  font-style: italic;
  overflow: auto;
}

p {
  margin-top: 0;
}

.App-header::-webkit-scrollbar,
.fact-container::-webkit-scrollbar {
  display: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
